import * as React from "react";
import {ArcSeries, FlexibleWidthXYPlot} from "react-vis";
import {Paper, Typography, ListItem, ListItemIcon, ListItemText, Skeleton, Grid, Avatar} from "@mui/material";
import {makeStyles} from "@mui/styles";
import {
    Autorenew,
    Whatshot
} from "@mui/icons-material";
import {CoineusCryptoFormat} from "../utils/currency_format";

const useStyles = makeStyles((theme) => ({
    xyplot: {
        margin: '0 auto',
    },
}));

export default function SRGSupplyChart(props) {
    const classes = useStyles();

    const {supply, burned, liquidity, top10, srgone, srgoneBurned, otherSrg20} = props;

    const colors = {
        supply: '#FFF',
        burned: '#fa5700',
        srgone: '#3661FF'
    }

    const top10Colors = ['#f00', '#ff7f00', '#ff0', '#7fff00', '#0f0', '#00ff7f', '#0ff', '#007fff', '#00f', '#7f00ff'];

    const divider = 360 * .0075 * (Math.PI / 180);

    const cSupply = supply - burned;

    const aBurned = 360 * (burned / supply) * (Math.PI / 180);

    const aSRGONE = 360 * (srgone / cSupply) * (Math.PI / 180);
    const aLiquidity = 360 * (liquidity / cSupply) * (Math.PI / 180);
    const aOtherSrg20 = 360 * (otherSrg20 / cSupply) * (Math.PI / 180);

    const angles = {

        burnedStart: 0,
        burnedEnd: aBurned,

        liquidityStart: aBurned + divider,
        liquidityEnd: aBurned + aLiquidity,

        srgoneStart: aBurned + aLiquidity + divider,
        srgoneEnd: aBurned + aLiquidity + aSRGONE + divider,

        supplyStart: aBurned + divider,
        supplyEnd: 360 * (Math.PI / 180) - divider,
    }

    const aTop10 = [];
    let aTop10start = aBurned + aLiquidity + aSRGONE + divider + divider;
    top10.map((t,i) => {
        let thisAngle = (360 * (t.totalLiquiditySRG / cSupply) * (Math.PI / 180))

        aTop10.push(
            {
                angle0: aTop10start,
                angle: aTop10start + thisAngle,
                opacity: 1,
                radius: 7.5,
                radius0: 7.25,
                color: top10Colors[i],
            }
        )
        aTop10start += thisAngle + divider;
    })

    aTop10.push(
        {
            angle0: aTop10start,
            angle: aTop10start + aOtherSrg20,
            opacity: 1,
            radius: 7.5,
            radius0: 7.25,
            color: colors.srgone,
        }
    )

    if (
        supply === undefined
    ) {
        return <Paper style={{padding: 20}}>
            <Skeleton variant="circular" height={180} width={180} style={{margin: '0 auto'}}/>
        </Paper>;
    }

    return (
        <>
            <FlexibleWidthXYPlot
                xDomain={[-5, 5]}
                yDomain={[-5, 5]}
                height={250}
                margin={{left: 0, right: 0, top: 0, bottom: 0}}
                className={classes.xyplot}
            >
                <ArcSeries
                    radiusDomain={[0, 10]}
                    center={{x: 0, y: 0}}
                    data={[
                        {
                            angle0: angles.supplyStart,
                            angle: angles.supplyEnd,
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.supply,
                        },
                        {
                            angle0: angles.burnedStart,
                            angle: angles.burnedEnd,
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.burned,
                        },
                        {
                            angle0: angles.liquidityStart,
                            angle: angles.liquidityEnd,
                            opacity: 1,
                            radius: 7.5,
                            radius0: 7.25,
                            color: colors.srgone,
                        },
                        {
                            angle0: angles.srgoneStart,
                            angle: angles.srgoneEnd,
                            opacity: 1,
                            radius: 7.5,
                            radius0: 7.25,
                            color: colors.srgone,
                        },
                            ...aTop10
                    ]}
                    colorType={'literal'}
                />
            </FlexibleWidthXYPlot>

            <Typography
                style={{
                    height: 0,
                    width: '100%',
                    textAlign: 'center',
                    top: -155,
                    position: 'relative',
                    fontSize: 30,
                    fontWeight: '700'
                }}
            >
                {((burned / supply) * 100).toFixed(2)}
                <span
                    style={{
                        fontSize: 12,
                        fontWeight: '400',
                        marginTop: -10,
                        display: 'block',
                    }}
                >
          % BURNED
        </span>
            </Typography>

            <Grid container>

                <Grid item xs={6}>
                    <StatListItem icon={<Autorenew style={{color: colors.supply}}/>} color={colors.supply}
                                  value={supply}
                                  label="SUPPLY"/>
                </Grid>
                <Grid item xs={6}>
                    <StatListItem icon={<Whatshot style={{color: colors.burned}}/>} color={colors.burned}
                                  value={burned}
                                  label="BURNED"/>
                </Grid>

                <Grid item xs={6}>
                    <StatListItem icon={<Autorenew style={{color: colors.supply}}/>} color={colors.supply}
                                  value={cSupply}
                                  label="CIRCULATION"/>
                </Grid>
                <Grid item xs={6}>
                    <StatListItem
                        icon={<Avatar
                            src={`https://coineus.app/assets/tokens/bnb/0x9f19c8e321bD14345b797d43E01f0eED030F5Bff/logo.png`}
                            sx={{width: 24, height: 24}}/>}
                        color={colors.srgone}
                        value={liquidity}
                        label="LIQUIDITY"/>
                </Grid>
                <Grid item xs={6}>
                    <StatListItem
                        icon={<Avatar
                            src={`https://coineus.app/assets/tokens/bnb/0x14F91F92aF274DDFF7f1A7f187B5eB5385F5f772/logo.png`}
                            sx={{width: 24, height: 24}}/>}
                        color={colors.srgone}
                        value={srgone}
                        label="SRGONE"/>
                </Grid>
            </Grid>

        </>
    );
}

function StatListItem(props) {
    const {color, value, label, icon} = props;

    return <ListItem disablePadding>
        <ListItemIcon style={{paddingLeft: 15}}>
            {icon}
        </ListItemIcon>
        <ListItemText
            primary={CoineusCryptoFormat(value)}
            secondary={label}
            primaryTypographyProps={{fontWeight: 700, style: {color}}}
            secondaryTypographyProps={{fontSize: '.75rem', style: {color}}}
        />
    </ListItem>;
}
