import {useEffect, useState} from "react";
import axios from "axios";
import {
    Avatar, Divider,
    Grid,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    ListSubheader,
    Paper, Toolbar,
    Typography
} from "@mui/material";
import Web3 from "web3";
import {CoineusCryptoFormat} from "../utils/currency_format";
import {useSelector} from "react-redux";

import srg_abi from "../abis/srg_abi.json";
import srgone_abi from "../abis/Coineus/SRGONE.json"
import {makeBatchRequest} from "../utils/promisify";
import SRGSupplyChart from "./SRGSupplyChart";

export default function SRGSupply(props) {

    const {rpc} = useSelector( state => state.coineus);

    const [tokens, setTokens] = useState([]);
    const [total, setTotal] = useState([]);
    const [totalTop10, setTotalTop10] = useState([]);
    const [top10List, setTop10List] = useState([]);
    const [totalOther, setTotalOther] = useState([]);
    const [srgData, setSrgData] = useState({})

    const top10Colors = ['#f00', '#ff7f00', '#ff0', '#7fff00', '#0f0', '#00ff7f', '#0ff', '#007fff', '#00f', '#7f00ff'];

    useEffect(() => {
        axios.post(
            "https://api.thegraph.com/subgraphs/name/somemoecoding/surgeswap-v2",
            {
                query: `
                    {
                      tokens(
                        where:{
                          id_not:"0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe"
                      }
                        orderBy: totalLiquiditySRG
                        orderDirection: desc
                      ){
                        id
                        name
                        symbol
                        tradeVolumeUSD
                        totalLiquiditySRG
                      }
                    }
            `
            }
        ).then(resp => {
            const {tokens} = resp.data.data;

            tokens.sort((a, b) => {
                if (parseFloat(a.totalLiquiditySRG) > parseFloat(b.totalLiquiditySRG)) {
                    return -1;
                }
                if (parseFloat(a.totalLiquiditySRG) < parseFloat(b.totalLiquiditySRG)) {
                    return 1;
                }
                return 0;
            })

            let count = 0;
            let totalTop10 = 0;
            let top10List = [];
            let totalOther = 0;
            tokens.map(t => {
                count++
                if (count > 10) {
                    totalOther += t.totalLiquiditySRG / 1;
                } else {
                    totalTop10 += t.totalLiquiditySRG / 1;
                    top10List.push(t)
                }
            });

            setTotalTop10(totalTop10)
            setTop10List(top10List)
            setTotalOther(totalOther)
            setTotal(totalTop10 + totalOther)

            setTokens(tokens)
        })


        const web3 = new Web3(rpc.bnb);
        const srg_contract = new web3.eth.Contract(srg_abi, '0x9f19c8e321bD14345b797d43E01f0eED030F5Bff');
        const srgone_contract = new web3.eth.Contract(srgone_abi, '0x14F91F92aF274DDFF7f1A7f187B5eB5385F5f772');
        const DEAD = '0x000000000000000000000000000000000000dEaD';

        const calls = [
            srg_contract.methods.totalSupply().call,
            srgone_contract.methods.totalSupply().call,
            srg_contract.methods.balanceOf('0x9f19c8e321bD14345b797d43E01f0eED030F5Bff').call,
            srg_contract.methods.balanceOf(DEAD).call,
            srgone_contract.methods.balanceOf(DEAD).call,
        ]

        makeBatchRequest(web3, calls).then(resp => {
            const [
                totalSupplySRG,
                totalSupplySRGONE,
                totalSRGLP,
                totalBurnedSRG,
                totalBurndedSRGONE
            ] = resp;

            setSrgData({
                totalSRGLP : totalSRGLP / (10**9),
                totalSupplySRG : totalSupplySRG/(10**9),
                totalSupplySRGONE : totalSupplySRGONE/(10**9),
                totalBurnedSRG : totalBurnedSRG/(10**9),
                totalBurnedSRGONE : totalBurndedSRGONE/(10**9)
            })
        });

    }, [])

    return (<Grid container spacing={2} style={{marginTop:10}}>
        <Grid item xs={12}>
            <Paper>

                <SRGSupplyChart
                    supply={srgData.totalSupplySRG}
                    burned={srgData.totalBurnedSRG}
                    liquidity={srgData.totalSRGLP}
                    top10={top10List}
                    srgone={srgData.totalSupplySRGONE}
                    srgoneBurned={srgData.totalBurnedSRGONE}
                    otherSrg20={totalOther}
                />

                <List dense>
                    <ListSubheader>SRG20 BNB TOP 10</ListSubheader>
                    {
                        top10List.map((t,i) => {
                            return <ListItem key={t.id}>
                                <ListItemAvatar style={{minWidth:30}}>
                                    <Avatar
                                        sx={{width: 24, height: 24}}
                                        style={{border: `1px solid ${top10Colors[i]}`}}
                                        src={`https://coineus.app/assets/tokens/bnb/${Web3.utils.toChecksumAddress(t.id)}/logo.png`}
                                    />
                                </ListItemAvatar>
                                <ListItemText primary={t.name}/>
                                <ListItemText
                                    primary={CoineusCryptoFormat(t.totalLiquiditySRG)}
                                    primaryTypographyProps={{textAlign: 'right'}}
                                />
                            </ListItem>
                        })
                    }

                    <ListItem>
                        <ListItemAvatar style={{minWidth:30}}>
                            <Avatar
                                sx={{width: 24, height: 24}}
                                style={{border: `1px solid #3661FF`}}
                                src={`https://coineus.app/assets/tokens/bnb/0x9f19c8e321bD14345b797d43E01f0eED030F5Bff/logo.png`}
                            />
                        </ListItemAvatar>
                        <ListItemText primary="All Other SRG20"/>
                        <ListItemText
                            primary={CoineusCryptoFormat(totalOther)}
                            primaryTypographyProps={{textAlign: 'right'}}
                        />
                    </ListItem>
                </List>
            </Paper>
        </Grid>
    </Grid>);
}