import {useSelector} from "react-redux";
import {
    Button,
    Dialog,
    Grid,
    ListItem,
    ListItemText,
    Paper,
    Typography,
    IconButton,
    List,
    Tabs,
    Tab,
    Box,
    FormControl,
    TextField, InputAdornment,
} from "@mui/material";

import Web3 from "web3";
import srg_abi from '../../abis/srg_abi.json';
import srgone_abi from '../../abis/Coineus/SRGONE.json';

import {useState} from "react";

import SwapVerticalCircleIcon from '@mui/icons-material/SwapVerticalCircle';

import CoineusLoading from "../CoineusLoading";
import {CoineusCryptoFormat} from "../../utils/currency_format";
import {toWei} from "../../utils/format";

const SRG_CONTRACT_ADDRESS = "0x9f19c8e321bD14345b797d43E01f0eED030F5Bff"
const SRGONE_CONTRACT_ADDRESS = "0x14F91F92aF274DDFF7f1A7f187B5eB5385F5f772"

export default function SRGONE(props) {

    const {wallet} = useSelector(state => state.coineus);

    const {onSuccess} = props;

    const [open, setOpen] = useState(false);
    const [pending, setPending] = useState(false);
    const [amount, setAmount] = useState("");
    const [action, setAction] = useState('deposit');

    const deposit = async () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const srg_contract = new web3.eth.Contract(srg_abi, SRG_CONTRACT_ADDRESS);
            const contract = new web3.eth.Contract(srgone_abi, SRGONE_CONTRACT_ADDRESS);


            srg_contract
                .methods
                .allowance(wallet.address, SRGONE_CONTRACT_ADDRESS)
                .call((err, resp) => {

                    if (err) return;

                    if ((resp / (10 ** 9)) < amount) {
                        srg_contract
                            .methods
                            .approve(SRGONE_CONTRACT_ADDRESS, toWei(amount, 9))
                            .send({from: wallet.address})
                            .once('transactionHash', function (tx) {
                                setPending(true)
                            })
                            .on('error', function (error) {
                                setPending(false)
                            })
                            .then(function (receipt) {
                                setPending(false)
                                deposit()
                            });
                    } else {
                        deposit()
                    }
                })

            const deposit = () => {
                contract.methods.deposit(toWei(amount, 9))
                    .send({from: wallet.address})
                    .once('transactionHash', function (tx) {
                        setPending(true)
                    })
                    .on('error', function (error) {
                        setPending(false)
                    })
                    .then(function (receipt) {
                        onSuccess();
                        setAmount('');
                        setOpen(false);
                        setPending(false);
                    });
            }
        }
    }

    const withdraw = async () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(srgone_abi, SRGONE_CONTRACT_ADDRESS);


            contract
                .methods
                .allowance(wallet.address, SRGONE_CONTRACT_ADDRESS)
                .call((err, resp) => {

                    if (err) return;

                    if ((resp / (10 ** 9)) < amount) {
                        contract
                            .methods
                            .approve(SRGONE_CONTRACT_ADDRESS, toWei(amount, 9))
                            .send({from: wallet.address})
                            .once('transactionHash', function (tx) {
                                setPending(true)
                            })
                            .on('error', function (error) {
                                setPending(false)
                            })
                            .then(function (receipt) {
                                setPending(false)
                                withdraw()
                            });
                    } else {
                        withdraw()
                    }
                })

            const withdraw = () => {
                contract.methods.withdraw(toWei(amount, 9))
                    .send({from: wallet.address})
                    .once('transactionHash', function (tx) {
                        setPending(true)
                    })
                    .on('error', function (error) {
                        setPending(false)
                    })
                    .then(function (receipt) {
                        onSuccess();
                        setAmount('');
                        setOpen(false);
                        setPending(false);
                    });
            }
        }
    }

    return <>

        <IconButton
            disabled={
                wallet.chain !== 56
            }
            edge="end"
            onClick={() => setOpen(true)}
            color="primary"
        >
            <SwapVerticalCircleIcon/>
        </IconButton>

        <Dialog open={open} fullWidth maxWidth="sm">
            <Grid container spacing={2}>
                <Grid item xs={12}>
                    <Paper>
                        <Tabs
                            variant="fullWidth"
                            value={action}
                            onChange={(ev, val) => {
                                setAction(val)
                                setAmount('')
                            }}
                        >
                            <Tab id="deposit" value="deposit" label="Deposit"/>
                            <Tab id="withdrawal" value="withdrawal" label="Withdrawal"/>
                        </Tabs>
                        {
                            action === "deposit" && <Box style={{padding: 20}}>
                                <List>
                                    <ListItem disableGutters disablePadding>
                                        <ListItemText
                                            secondary="Balance"
                                        />
                                        <ListItemText
                                            secondary={`${CoineusCryptoFormat(wallet.balances.bnb[SRG_CONTRACT_ADDRESS])} SRG`}
                                            secondaryTypographyProps={{textAlign: 'right'}}
                                        />
                                    </ListItem>
                                </List>
                                <FormControl fullWidth style={{marginBottom: 20}}>
                                    <TextField
                                        autoComplete="off"
                                        type="number"
                                        value={amount}
                                        placeholder="Enter Amount"
                                        onChange={(ev) => {
                                            setAmount(ev.target.value)
                                        }}
                                        InputProps={{
                                            endAdornment: <InputAdornment position="end">
                                                <Button variant="contained" onClick={() => {
                                                    setAmount(wallet.balances.bnb[SRG_CONTRACT_ADDRESS])
                                                }
                                                }>MAX</Button>
                                            </InputAdornment>,
                                        }}
                                    />
                                </FormControl>
                                <Button fullWidth variant="contained"
                                        disabled={!amount || amount > wallet.balances.bnb[SRG_CONTRACT_ADDRESS]}
                                        onClick={deposit}>Deposit</Button>
                            </Box>
                        }

                        {
                            action === "withdrawal" && <Box style={{padding: 20}}>
                                <List>
                                    <ListItem disableGutters disablePadding>
                                        <ListItemText
                                            secondary="Balance"
                                        />
                                        <ListItemText
                                            secondary={`${CoineusCryptoFormat(wallet.balances.bnb[SRGONE_CONTRACT_ADDRESS])} SRGONE`}
                                            secondaryTypographyProps={{textAlign: 'right'}}
                                        />
                                    </ListItem>
                                </List>
                                <FormControl fullWidth style={{marginBottom: 20}}>
                                    <TextField
                                        autoComplete="off"
                                        type="number"
                                        value={amount}
                                        placeholder="Enter Amount"
                                        onChange={(ev) => {
                                            if (ev.target.value > wallet.balances.bnb[SRGONE_CONTRACT_ADDRESS]) return;
                                            setAmount(ev.target.value)
                                        }}
                                        InputProps={{
                                            endAdornment: <InputAdornment position="end">
                                                <Button variant="contained" onClick={() => {
                                                    setAmount(wallet.balances.bnb[SRGONE_CONTRACT_ADDRESS])
                                                }
                                                }>MAX</Button>
                                            </InputAdornment>,
                                        }}
                                    />
                                </FormControl>
                                <Button fullWidth variant="contained"
                                        disabled={!amount || amount > wallet.balances.bnb[SRGONE_CONTRACT_ADDRESS]}
                                        onClick={withdraw}>Withdrawal</Button>
                            </Box>
                        }
                        <Box style={{padding: 20, paddingTop: 0}}>

                            <Button fullWidth
                                    onClick={() => setOpen(false)}>Cancel</Button>
                        </Box>

                        <Box style={{padding: 20, paddingTop: 0, textAlign: 'center'}}>
                            <Typography variant="caption" color="primary">1% Fee on Deposits and
                                Withdrawals</Typography>
                        </Box>

                    </Paper>
                </Grid>

            </Grid>
        </Dialog>

        <CoineusLoading open={pending} label="Transaction Pending"/>
    </>
}
